.navigation {
	font-family: 'Montserrat Alternates', sans-serif;
	font-weight: 700;
	display: flex;
	align-items: center;
	margin: 0 -10px;

	a {
		position: relative;
		top: 0;
		left: 0;
		padding: 0 10px;
	}
	a:hover {
		color: red;
		transition: all 0.2s ease;
	}
	.active::after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -3px;
		width: 100%;
		height: 2.5px;
		border-radius: 5px;
		background-color: red;
	}
}

@media (max-width: 500px) {
	.navigation {
		font-size: 15px;
		margin: 0 -5px 5px -5px;
		a {
			padding: 0 5px;
		}
	}
}
